.rootWrapper{
    background-color: rgb(34,34,34);
}
.wrapper{
    display: grid;
    grid-template-columns: 1fr 2fr 2fr 2fr 1fr;
    grid-template-rows: 1fr 2fr 1fr;
    gap: 0px 20px;
    width: 100%;
    height: 200px;
    background-color: rgb(34,34,34);
    grid-template-areas:
      ". . . . ."
      ". TikTok Vk Instagram ."
      ". . . . .";
}

.footerIconTikTok{
    grid-area: TikTok;
}

.footerIconVk{
    grid-area: Vk;
}

.footerIconInstagram{
    grid-area: Instagram;
}

.footerIcon{
    max-width: 100%;
    max-height: 100%;
}

.footerLabel{
    position: relative;
    top: 30px;
    background-color: rgb(34,34,34);
    color: rgb(231, 231, 231);
    font-family: 'Open Sans', sans-serif;
    text-align: center;
}

.labelText{
    position: relative;
    top: 30px;
}

.copyrightLabel{
    font-family: 'Open Sans', sans-serif;
    position: relative;
    bottom: 0;
    text-align: center;
    color: rgb(85, 85, 85);
    font-size: 20px;
}