.wrapper {
    display: grid;
    grid-template-columns: 1fr 10fr 1fr;
    grid-template-rows: 1fr;
    gap: 0px 0px;
    background-color: rgb(34,34,34);
    width: 100%;
    height: auto;
    font-family: 'Open Sans', sans-serif;
    grid-template-areas:
      ". content . ";
 
  }

.content{
    grid-area: content;
}

.title{
    color: rgb(246,203,75);
    font-size: 56px;
    text-align: center;
    z-index: 3;
}

.description{
    
    color: rgb(231, 231, 231);
    text-align: left;
    margin-bottom: 20px;
    font-size: 22px;
    z-index: 999;
}

.DecorationLayerWrapperYC{
    position: absolute;
    width: 300px;
    height: 300px;
    top: 200vh;
    left: 15vw;
    
}

.DecorationImgYC{

    max-width: 100%;
    max-height: 100%;
}

.DecorationLayerWrapperYH{
    position: absolute;
    width: 150px;
    height: 150px;
    top: 145vh;
    left: 35vw;
    z-index: 2;
}

.DecorationImgYH{
    z-index: 2;
    max-width: 100%;
    max-height: 100%;
}

.titleWrapper{
    z-index: 3;
}

h2{
    z-index: 999;
}