
.wrapper{
    width: 100vw;
    height: 650px;
    background-color: #8C52FF;
    z-index: 1;
  }


.titleLabel{
    font-family: 'Open Sans', sans-serif;   
    color: #F6CB4B;
    font-size: 30px;
    text-align:center;
    position: relative;
    top: 30px;
    padding-bottom: 100px;
}

.buyTicketButton{
    width: 80%;
    left: 10%;
    position: relative;
    color: white;
    border-radius: 0;
    margin-bottom: 30px;
}


.descriptionWrapper{
    display: grid;
    grid-template-columns: 1fr 10fr 1fr;
    grid-template-rows: 1fr;
    gap: 0px 0px;
    grid-template-areas:
      ". content .";
}

.description{
    z-index: 3;
    grid-area: content;
    color: rgb(231, 231, 231);
    font-family: 'Open Sans', sans-serif;   
}

.DecorationLayerWrapperDS{
    position: absolute;
    width: 300px;
    height: 300px;
    top: 135vh;
    left: 15vw;
    z-index: 2;
}

.DecorationImgDS{
    z-index: 2;
    max-width: 100%;
    max-height: 100%;
}

.buttonWrapper{
    position: absolute;
    width: 100%;
    top: 110%;
    margin: 0 auto;
    z-index: 3;
}

.ticketButton{
    width: 350px;
    margin:0 auto;
    display:block;
    height: 50px;
    font-family: 'Open Sans', sans-serif;
    text-align: center;
    font-size: 24px;
    border: none;
    background-color: #ffffff ;
    color: rgb(187, 187, 187);
    z-index: 3;
}